.uiLeftBar {
  /* background-color: rgb(245, 244, 244); */
  width: 100%;
  height: 100%;
  padding: 30px;
  border-radius: 20px;
}

.dsgnPreview {
  border-color: red 2px salmon;
}

.partSelector {
  width: 100%;
}

.partSelectorMenu {
  width: 100%;
  height: 30px;
  border-radius: 10px;
  background-color: whitesmoke;
}

.uploadLogoContainer {
  background-color: rgb(255, 255, 255);
  width: 100%;
  height: 50%;
  border-radius: 20px;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  border-style: dashed;
  border: rgb(157, 157, 157) 1px solid;
  padding: 10px;
}

.uploadLogoContainer:hover {
  cursor: pointer;
}

.uploadLogoContainer > div > p {
  margin-top: 10px;
  margin-bottom: 20px;
}

.leftBarLogoH2 {
  margin-top: 20px;
}

.uploadedStickerContainer {
  display: flex;
  gap: 5px;
}
.uploadedStickerWrapper {
  border-radius: 5px;
  border: 1px grey solid;
  padding: 5px;
  margin-top: 10px;
  display: flex;
  width: 60px;
  height: 60px;
  display: flex;

  align-items: center;
  justify-content: center;
}

.uploadedSticker {
  width: 50px;
}
