.uiContainer {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  /* border: red 2px solid; */
}

.uiMainContent {
  display: flex;
  /* border: purple 2px solid; */
  height: 90%;
}

.uiLeftBarContainer {
  flex: 1;
  height: 100%;
  padding: 30px 20px;
}

.uiLeftBarWrapper {
  background-color: rgb(244, 244, 244);
  width: 100%;
  height: 100%;
  border-radius: 20px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.uiLeftBarWrapper > p {
  text-align: center;
  margin-bottom: 10px;
}

.uiCanvasnButtons {
  flex: 4;
  /* border: purple 3px solid; */
  display: flex;
  flex-direction: column;
  padding: 30px 20px;
}

.uiCanvasContainer {
  flex: 3.5;
  /* padding: 30px; */
  /* border: orange 2px solid; */
}

.uiButtonsContainer {
  flex: 1;
  display: flex;
  justify-content: space-around;
  align-items: center;
  /* border: red 2px solid; */
  /* padding: 30px; */
  /* margin-bottom: 30px; */
  gap: 10px;
  padding-top: 10px;
}
